

.weather-outlook{
   width: 100%;
   height: 30%;
   font-family: 'Sarabun', sans-serif;
   font-size: 0.9rem;
   color:  black;
   border-top: 3px solid white;
 

 
  

    h2{
        font-family: 'Montserrat', sans-serif;
        font-weight: 600;
        font-size: 0.9rem;
        color: white;
        margin: 1% 0 0 2%;
     
    }

    .glyph-container{
        margin: 2% 0 0 0 ;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: baseline;
       

        .info-glyph{
            width: 100px;
            height: 100px; 
            display:flex;
            flex-direction: column;

            span{
                width: 75px;
                height: 75px;
                font-family: 'Montserrat', sans-serif;
                font-weight: 600;
                color: white;
                background-color: inherit;
                margin: 0 auto;
                font-size: 2rem;
                border-radius: 50%;
                padding: 1.5rem;
                z-index: 10;
                
             }
             p{
                font-family: 'Montserrat', sans-serif;
                font-weight: 600;
                font-size: 0.7rem;
                position: absolute;
                top: 85%;
                right: 8%;

             }
         
             h4{
                font-family: 'Montserrat', sans-serif;
                font-weight: 600;
                min-width: 15rem;
                color: white;
                font-size: 0.8rem;
                margin: 10% 0 0 30%;
         
             }
        }

    }
}


@media screen and (max-width: 500px) {

    .weather-outlook {
        h2{
            font-size: 1.2rem;
        }

        .glyph-container{
            margin: 10% 0 0 0;

            .info-glyph{

                span{
                    font-size: 1.5rem;

                }

                p{
                    top: 83%;
                    right: 5%;
                }

                

                h4{
                    margin: 10% 0 0 15%;
                 
                }
            }
        }
    }
}

@media screen and  (max-width: 980px) and  (min-width: 530px) {
    .weather-outlook {
        h2{
            font-size: 1.5rem;
        }

        .glyph-container{
            //margin: 10% 0 0 0;

            .info-glyph{

                span{
                    font-size: 1.8rem;

                }

                p{
                    top: 85%;
                    right: 8%;
                }

                

                h4{
                    font-size: 1rem;
                    margin: 10% 0 0 18%;
                 
                }
            }
        }
        
    }
}