

.weather-card{
    width: 1000px;
    height: 600px;
    margin: 8% 18%;
    background: rgba(255, 255, 255, 0.44);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(11.1px);
    -webkit-backdrop-filter: blur(11.1px);
    border: 1px solid rgba(255, 255, 255, 0.51);
    box-shadow: rgba(255, 255, 255, 0.1) 0px 1px 1px 0px inset, rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
    background: #00c6ff;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #4B92EE, #336CD9);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #4B92EE, #336CD9); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

.search-bar{
    width: 1000px;
    height: 50px;
    position: absolute;
    z-index: 30;
    top: -15%;
    background-color: #E0E2E7;

    .search-text{
        width: 70%;
        height: 100%;
        background-color: white;
        margin: 0 0 0 15%;
        outline: none;
        border: none;
        border-radius: 1.5rem;
        padding: 1.5rem;
        position: relative;
    }

    .search-submit{
        width: 5rem;
        height: 2.5rem;
        background-color: turquoise;
        border: none;
        border-radius: 1.5rem;
        position: absolute;
        top: 10%;
        left: 76.5%;
        font-size: 0.9rem;
        color: white;
        cursor: pointer;

      &:hover{
        opacity: 0.7;
      }
    }

    .search-reset {
        width: 2rem;
        height: 2rem;
        cursor: pointer;
        position: absolute;
        background-color: inherit;
        border: 2px solid #0E6251  ;
        color: #0E6251  ;
        border-radius: 0.2rem;
        top: 25%;
        left: 80%;

        &:hover {
            background-color: #0E6251 ;
            color: white;
        }
    }
}



}

@media screen and (max-width: 500px){
    .weather-card{
        width: 400px;
        height: 900px;
        display: flex;
        flex-direction: column;
        margin: 45% 5%;


        .search-bar{
            width: 600px;
            height: 40px;
            margin: 15% -25%;
        }
    }
}

@media screen and  (max-width: 980px) and (min-width: 530px) {
    .weather-card {
        width: 850px;
        height: 700px;
        flex-direction: column;
        margin: 20% 10%;
      
    }
    .search-bar{
        width: 700px;
        height: 40px;
        margin: 2% -10%;
    }
}

